/*
  Animation
*/
.c-modal {
  position: fixed;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 999999999;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: auto;
  padding: 2.5rem 0;
  background-color: rgba(34, 34, 34, 0.2);
}
.c-modal .modal-container {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
    border: none;
    background: white;
    overflow: auto;
    border-radius: 0px;
    outline: none;
    padding: 0px;
    margin: auto;
}
.c-modal .close-modal {
    position: absolute;
    right: 1.25rem;
    top: 1.25rem;
    z-index: 1;
}
.c-modal .close-modal svg {
      width: 25px;
      height: 25px;
}
.c-modal .modal-content {
    position: relative;
    background-color: #FFFFFF;
    padding: 54px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
}



