/*
  Animation
*/
.c-button {
  font-size: 0.875rem;
  text-transform: uppercase;
  text-align: center;
  background-color: transparent;
  color: #454545;
  padding: 22px;
  border-radius: 2px;
  outline: none;
}
.c-button.-filled {
    background-color: #334285;
    font-weight: 700;
}
.c-button.-filled.-alternative {
      background-color: #222222;
}
.c-button.-full {
    width: 100%;
}
.c-button.-bordered {
    border: solid 1px #FFFFFF;
}
.c-button.-big {
    width: 245px;
}
.c-button.-small {
    width: 196px;
}
.c-button:active {
    opacity: 0.7;
}
.c-button:hover {
    color: #252525;
}
.c-button.-white {
    color: #FFFFFF;
}
.c-button.-dark {
    border-color: #334285;
    color: #334285;
}



